import React from 'react';

import {TextGroup, Important, Title, SubTitle, Line} from '../../components/TextUtils/TextUtils.js';

var version = "1.1.0";

var UpdateTitle = () => {
    return "TwitchLink " + version;
};

var UpdateNote = () => {
    return (
        <div>
            <SubTitle>Update Note</SubTitle>
            <div style={{padding: "30px 10px"}}>
                <TextGroup>
                    <Title>Massive Update</Title>
                    <Line/>
                    [General]
                    <br/>
                    <br/>
                    <Important>Fixed UI not displaying correctly on some devices and resolutions.</Important>
                    <br/>
                    <Important>You can now freely resize the window.</Important>
                    <br/>
                    Fixed 'Program not responding' issue.
                    <br/>
                    Temporary files that were not deleted due to abnormal termination are deleted in the next execution.
                    <br/>
                    A software security patch has been applied.
                    <br/>
                    Software optimized and stabilized.
                    <br/>
                    <br/>
                    <br/>
                    [Search]
                    <br/>
                    <br/>
                    <Important>Improved channel search speed.</Important>
                    <br/>
                    <Important>URL Search now recognizes more various URLs.</Important>
                    <br/>
                    You can now view previous broadcast history in Channel tab.
                    <br/>
                    Fixed UI freezing while loading video list.
                    <br/>
                    <Important>You can save thumbnails from the More menu.</Important>
                    <br/>
                    <br/>
                    <br/>
                    [Settings]
                    <br/>
                    <br/>
                    <Important>Removed file extension restrictions.</Important>
                    <br/>
                    Several variables have been added to the filename template, including 'resolution'.
                    <br/>
                    <br/>
                    <br/>
                    [Download]
                    <br/>
                    <br/>
                    <Important>You can now adjust the download speed.</Important>
                    <br/>
                    <Important>Downloads can be paused.</Important>
                    <br/>
                    You can choose whether to unmute the video.
                    <br/>
                    <Important>Temporary storage is no longer required for super-fast downloads.</Important>
                    <br/>
                    You can now see the waiting time of update tracking mode.
                    <br/>
                    You can now skip waiting in update tracking mode.
                    <br/>
                    Now you can skip directly to encoding if your download progress is over 90%.
                    <br/>
                    <Important>You can now see the download status directly in the taskbar.</Important>
                    <br/>
                    Improved video cropping.
                    <br/>
                    You can now see exactly how long and cropped the video you are downloading.
                    <br/>
                    Audio Only resolutions are now saved as audio files.
                    <br/>
                    <Important>Fixed an issue where the program freezes when downloading some long videos.</Important>
                    <br/>
                    <Important>Fixed an issue where mp4 files would not play if interrupted while downloading.</Important>
                </TextGroup>
            </div>
        </div>
    );
};

var SourceUrl = "https://github.com/devhotteok/TwitchLink/releases/tag/" + version;

export default {
    UpdateTitle,
    UpdateNote,
    SourceUrl
}